.img-fluid {
    max-width: 100%;
    height: auto;
}

#b_partner .img-box {
  padding: 40px 40px 10px 40px;
  position: relative;
}
#b_partner .img-box i {
  font-style: normal;
  text-align: center;
  font-weight: bold;
  background: #18425A;
  padding: 3px;
  font-size: 15px;
  color: #ffffff;
  display: block;
  border-radius: 100%;
  height: 30px;
  width: 30px;
  position: absolute;
  top: 9%;
  left: 45%;
}
.b_partner-header .b_title {
  text-align: center;
  color: #333;
  line-height: 1.6;
  position: relative;
  margin-top: 5px;
  font-size: small;
}

.section-title{
  font-size: 4rem;
  font-weight: 400;
  line-height: 1.4;
  text-transform: uppercase;
  color: var(--dark-text);
  margin: 1.5rem 0;
}

.b_partner-header .b_title span {
  font-weight: 600;
}

@media screen and (max-width: 600px) {
  form input {
    width: 60%;
  }
}
